.app-wrapper {
  display: grid;

  width: 1200px;
  margin: 0 auto;

  grid-template-areas: 
  "header header"
  "nav content";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;

  row-gap: 10px;
  column-gap: 10px;
}


.app-wrapper-content {
	grid-area: content;
	background-color: rgb(228, 248, 193);
}







ul li{
  list-style-type: square;
}